<template>
  <div class="">
    <div class="top">
      <Row>
        <Col span="12">
          <h3></h3>
        </Col>
        <Col span="12">
          <div class="export">
            <Button type="ghost" icon="ios-cloud-upload-outline" @click="show_add"></Button>
          </div>
        </Col>
    </Row>
    </div>
    <div class="warp_content">
      <div class="item">
        <Date-picker v-model="listFilterStartTime" type="date" placeholder="" style="width: 200px;display:inline-block;margin: 0 20px;"></Date-picker>
        <span></span>
        <Date-picker v-model="listFilterEndTime" type="date" placeholder="" style="width: 200px;display:inline-block;margin: 0 20px;"></Date-picker>
        <Button type="info" @click="listFilter"></Button>
      </div>
      <Table :height="tableHeight" :columns="columns" :data="data" @on-row-click="gotoDetail"></Table>
      <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="total" :current="page" :page-size="limit" @on-change="changePage"></Page>
        </div>
      </div>
    </div>

    <left-page :show="showEdit" @on-close="closeEdit">
      <span slot="title"></span>
      <div slot="content" class="addContent">
        <Form :model="editFormItem" ref="editFormItem" :label-width="100" :rules="ruleEditValidate">
          <Form-item label="">
              <Date-picker type="date" placeholder="" v-model="editFormItem.check_date"></Date-picker>
          </Form-item>
          <Form-item label="" prop="check_type">
              <Select v-model="editFormItem.check_type" placeholder="">
                <Option value="early"></Option>
                <Option value="night"></Option>
              </Select>
          </Form-item>
          <Form-item label="" prop="userName">
              <Input v-model="editFormItem.userName" placeholder=""></Input>
          </Form-item>

          <Form-item>
              <Button type="primary" @click="submitEdit('editFormItem')"></Button>
              <Button type="ghost" style="margin-left: 8px" @click="cancelEdit"></Button>
          </Form-item>
        </Form>
      </div>
    </left-page>

    <left-page :show="showAdd" @on-close="close">
      <span slot="title"></span>
      <div slot="content" class="addContent">
        <Form :model="formItem" ref="formItem" :label-width="100" :rules="ruleValidate">
          <Form-item label="">
              <Date-picker type="date" placeholder="" v-model="formItem.check_date"></Date-picker>
          </Form-item>
          <Form-item label="" prop="check_type">
              <Select v-model="formItem.check_type" placeholder="">
                <Option value="early"></Option>
                <Option value="night"></Option>
              </Select>
          </Form-item>
          <Form-item label="" prop="userName">
              <Input v-model="formItem.userName" placeholder=""></Input>
          </Form-item>
          <Form-item>
              <Button type="primary" @click="submitAdd('formItem')"></Button>
              <Button type="ghost" style="margin-left: 8px" @click="cancelAdd"></Button>
          </Form-item>
        </Form>
      </div>
    </left-page>

    <left-page :show="showDetail" @on-close="closeDetail">
      <span slot="title" class="detailTitle">
        <span><a @click="editItem"></a>
        <a @click="deleteItem" style="margin: 0 10px;"></a></span>
      </span>
      <div slot="content" class="addContent">
        <div class="item">
          <h4></h4>
          <p>{{detail.check_date}}</p>
        </div>
        <div class="item">
          <h4></h4>
          <p>{{detail.check_type == 'early' ? '' : ''}}</p>
        </div>
        <div class="item">
          <h4></h4>
          <p>{{detail.userName}}</p>
        </div>
      </div>
    </left-page>
  </div>
</template>
<style media="screen" scoped>
  .export {
    position: absolute;
    right: 0;
    top: 10px;
    line-height: 1;
    display: inline-block;
  }
  .item {
   margin: 0 0 20px 10px;
 }
  .addContent {
    padding: 20px 20px 20px 10px;
    position: absolute;
    top: 50px;
    width: 100%;
    left: 0;
    bottom: 0;
    overflow: auto;
  }
  .detailTitle {
    position: relative;
    display: inline-block;
    width: 550px;
  }
  .detailTitle span {
    position: absolute;
    right: 10px;
  }
</style>
<script>
    import axios from 'axios'
    import leftPage from './leftPage';
    export default {
        data () {
            var defaultJurisdictions = ['', '', '', '', '', '', '', '', '', ''];
            return {
                showDetail: false,
                showEdit: false,
                tableHeight: '',
                showAdd: false,
                detail: {},
                editFormItem: {},
                listFilterStartTime: new Date().toLocaleDateString().replace(/\//ig, '-'),
                listFilterEndTime: new Date().toLocaleDateString().replace(/\//ig, '-'),
                limit: 20,
                page: 1,
                total: 0,
                ruleEditValidate: {
                  userName: [
                    { required: true, message: '', trigger: 'change' }
                  ],
                  check_type: [
                    { required: true, message: '', trigger: 'change' }
                  ]
                },
                formItem: {
                    userName: this.$root.user ? this.$root.user.rightName : '',
                    check_type: 'early',
                    check_date: this.$root.getLocalDate()
                },
                ruleValidate: {
                  name: [
                    { required: true, message: '', trigger: 'change' }
                  ],
                  password: [
                    { required: true, message: '', trigger: 'change' }
                  ]
                },
                columns: [
                    {
                        title: '',
                        width: 200,
                        key: 'check_date',
                        // fixed: 'left'
                    },
                    {
                        title: '',
                        width: 150,
                        key: 'check_type',
                        render(h, {row, column, index}) {
                          return `${row.check_type == 'early' ? '' : ''}`
                        }
                    },
                    {
                        title: '',
                        key: 'userName'
                    }
                ],
                data: []
            }
        },
        created() {
          this.listFilter();
        },
        methods: {
          listFilter() {
            let _this = this;
            this.$root.ajaxGet({
              funName: 'getOrderListLocation',
              params: {
                limit: this.limit,
                page: this.page,
                listFilterStartTime: this.$root.getLocalDate(this.listFilterStartTime),
                listFilterEndTime: this.$root.getLocalDate(this.listFilterEndTime)
              }
            }, function(res, initRes) {
              _this.data =  res;
              _this.total = initRes.count;
            })
          },
          editItem() {
            this.editFormItem = {
              check_date: this.detail.check_date,
              check_type: this.detail.check_type,
              userName: this.detail.userName,
              _id: this.detail._id
            };
            this.showEdit = true;
            this.showDetail = false;
          },
          cancelEdit() {
            this.showEdit = false;
          },
          submitEdit(name) {
            this.$refs[name].validate((valid) => {
              if (valid) {
                let _this = this;
                this.$root.ajaxPost({
                  funName: 'updateLocationItem',
                  params: _this.editFormItem
                }, function(res) {
                  _this.$Message.info('');
                  _this.showEdit = false;
                  _this.listFilter();
                })
              } else {
                this.$Message.error('!');
              }
            })
          },
          deleteItem() {
            let _this = this;
            this.$Modal.confirm({
              title: '',
              content: '',
              onOk() {
                _this.$root.ajaxPost({
                  funName: 'deleteLocationItem',
                  params: {
                    _id: _this.detail._id
                  }
                }, function(res) {
                  _this.showDetail = false;
                  _this.listFilter();
                })
              }
            });
          },
          closeEdit() {
            this.showEdit = false;
          },
            show_add() {
              this.showAdd = true;
            },
            close(value) {
              this.showAdd = !value;
            },
            submitAdd(name) {
              var _this = this;
              this.formItem.check_date = this.$root.getLocalDate(this.formItem.check_date).split(' ')[0];
              this.$refs[name].validate((valid) => {
                if (valid) {
                  this.$root.ajaxPost({
                    funName:'addLocation',
                    params: {
                      data: this.formItem
                    }
                  }, function(res) {
                    _this.listFilter();
                    _this.$Message.info('');
                    _this.showAdd = false;
                  })
                } else {
                  this.$Message.error('!');
                }
              })
            },
            cancelAdd() {
              this.showAdd = false;
            },
            gotoDetail(item) {
              this.detail = item;
              this.showDetail = true;
           },
           closeDetail() {
             this.showDetail = false;
           },
           changePage(value) {
             this.page = value;
             this.listFilter();
           },
        },
        watch: {
          'data'() {
            if(this.data.length > 10 && !this.tableHeight) {
              this.tableHeight = window.innerHeight;
            }
          }
        },
        components: {
          leftPage
        }
    }
</script>



