                break;
              case '6-2':
                window.location.href = '/#/hotelManagement';
                break;
              case '6-3':
                window.location.href = '/#/platformManagement';
                break;
              case '7-1':
                window.location.href = '/#/location';
                break;
              case '7-2':
                window.location.href = '/#/locationStatic';
                break;
              default:
                break;
            }
          }
